.heading {
  font-weight: 800;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #56ccf2;
  background: url(../images/rectangular.svg) no-repeat;
  padding-left: 37px;
  height: 21px;
  line-height: 21px;
  margin-bottom: 19px;

  &--white {
    color: #fff;
    background: url(../images/rectangular-white.svg) no-repeat;
    background-position-y: bottom;
  }
}
